import { isNil, fetchJson } from '../functions/PublicFunctions'

class Locales {
    defatultLocale = 'en-US';
    stringsMap = {};
    chooseLocale(){
      let lang = navigator.language;
      if (!isNil(navigator.languages) && navigator.languages.length > 0) {
        lang = navigator.languages[0];
      }
      let locale = window.Cookies.get('locale');
      if (!isNil(locale)) {
        lang = locale;
      }
      switch(lang.split('-')[0]) {
        case 'en':
          return 'en-US';
        case 'zh':
          return 'zh-CN';
        default:
          return this.defatultLocale;
      }
    }
  loadDefaultLocale() {
    this.stringsMap = require('./en-US.json');
    this.currentLocale = this.defatultLocale;
    this.fireCallbackIfNeeded();
  }
  // init method will load CLDR locale data according to currentLocale
  constructor(){
    let locale = this.chooseLocale();
    if (this.defatultLocale === locale) {
      this.loadDefaultLocale();
    }
    else {
      let filePath = '/locales/' + locale + '.json';
      fetchJson(filePath)
      .then(json => {
        this.stringsMap = json;
        this.currentLocale = locale;
        this.fireCallbackIfNeeded();
      })
      .catch(error => {
        console.log(error);
        this.loadDefaultLocale();
      });
    }
  }
  fireCallbackIfNeeded(){
    if (!isNil(this.cb)) { 
      this.cb(); 
      delete this.cb;
    }
  }
  waitWhileReady(callback){
    if (isNil(callback)) { return; }
    if (isNil(this.currentLocale)) {
      this.cb = callback;
    }
    else{
      callback();
    }
  }
  get(key, options) {
    let value = this.stringsMap[key];
    if (isNil(value)) {
        value = key;
    }
    if (!isNil(options)) {
      for (let k in options) {
        value = value.replace(`{${k}}`, options[k]);
      }
    }
    return value;
  }
}

window.Locales = new Locales();
window.L = window.Locales;
export default window.L;



