import React, { Component } from 'react';
import './Carousel.css';
import { fetchJson } from '../functions/PublicFunctions'

class Carousel extends Component {
  carouselIndicatorsComponent(items){
    if (items.length === 1){
      return ("");
    }
    let idx = 0;
    const carouselId = `#${this.props.id}`;
    const indicatorItems = items.map((item) => 
        <li data-target={carouselId} key={idx} data-slide-to={idx} className={idx++ === 0 ? "active": ""}></li>
      );
    return (
      <ol className="carousel-indicators">
        {indicatorItems}
      </ol>
    );
  }
  carouselSlidesComponent(items){
    let idx = 0;
    const slideItems = items.map((item) => 
          <div key={idx} className={idx++ === 0 ? "item active" : "item"}>
            <div className="Slide" style={{backgroundImage: `url(${item.bgImg})`}}></div>
            <div className="carousel-caption">
              <h1>{item.title}</h1>
              <p>{item.detail}</p>
            </div>
          </div>
        );
    return (
      <div className="carousel-inner">
        {slideItems}
      </div>
    );
  }
  carouselNextPrevious(hide) {
    if (hide) {
      return ("");
    }
    const carouselId = `#${this.props.id}`;
    return (
      <div>
        <a className="left carousel-control" href={carouselId} data-slide="prev"><span className="glyphicon glyphicon-chevron-left"></span></a>
        <a className="right carousel-control" href={carouselId} data-slide="next"><span className="glyphicon glyphicon-chevron-right"></span></a>
      </div>
    );
  }
  carouselComponent(items) {
    if (items.length === 0) {
      return ("");
    }
    // if (items.length > 4){
    //   items = items.slice(0, 4);
    // }
    return (
      <div id={this.props.id} className="carousel slide" data-ride="carousel"> 
        {this.carouselSlidesComponent(items)}
        {this.carouselIndicatorsComponent(items)}
        {this.carouselNextPrevious(items.length <= 1)}
      </div>
    );
  }
  createCarousel() {
    // let items = [
    // {"name":"Zebra Alphabet", "path":"/ios/alphabet/", "date":"02/13/2016", "version":"1.0.0.170522"},
    // {"name":"Web Reader", "path":"/ios/webreader/", "date":"02/13/2016", "version":"1.0.0.170522"},
    // ];
    // this.setState({carousel: this.carouselComponent(items)});
    fetchJson(this.props.dataPath)
    .then(json => {
      this.setState({carousel: this.carouselComponent(json)});
    })
    .catch(error => {
      this.setState({carousel: ""});
    });
  }
  constructor() {
    super();
    this.state = {
      carousel: ""
    };
  }
  componentDidMount(){
    this.createCarousel();
  }
  render() {
    return <div>{this.state.carousel}</div>;
  }
}

export default Carousel;



