import React, { Component } from 'react';
import logo from '../logo.svg';
import './AppHeader.css';
// import { Link } from 'react-router-dom'
import 'bootstrap.native'

const W = window;
const L = W.L;
class AppHeader extends Component {
  constructor() {
    super()
    this.state = {
      collapsed: true,
      localesOpened: false
    };
  }
  toggleCollapseMenu() {
    const collapsed = !this.state.collapsed;
    this.setState({collapsed});
  }
  toggleOpenLocales() {
    const localesOpened = !this.state.localesOpened;
    this.setState({localesOpened});
  }
  setCurrentLocale(event) {
    if (event.currentTarget.lang === L.currentLocale){ return null; }
    W.Cookies.set('locale', event.currentTarget.lang, Infinity);
    W.location.reload();
  }

  render() {

    const { collapsed, localesOpened } = this.state;
    const menuClass = collapsed ? 'navbar-collapse collapse' : 'navbar-collapse collapse in';
    const localesClass = localesOpened ? 'dropdown open' : 'dropdown';

    const { pathname } = W.location;
    const homeClass = pathname === '/' ? 'active' : '';
    const productsClass = pathname.match(/^\/products/) ? 'active' : '';
    const contactClass = pathname.match(/^\/contact/) ? 'active' : '';
    const aboutClass = pathname.match(/^\/about/) ? 'active' : '';

  

    const logoItem = (
      <div className="navbar-header">
        <button className="navbar-toggle" onClick={this.toggleCollapseMenu.bind(this)} type="button" data-toggle="collapse" data-target=".navbar-collapse">
          <span className="sr-only">Toggle navigation</span>
          <span className="icon-bar"></span>
          <span className="icon-bar"></span>
          <span className="icon-bar"></span>
        </button>
        <a className="navbar-brand" href="/" >
          <table><tbody><tr><td><img id="logo" src={logo} alt="Oueway" /></td><td>{L.get("BRAND")}</td></tr></tbody></table>
        </a>
      </div>
    );
    const ChinaFlag = (<span role="img" aria-label="China">🇨🇳中国</span>);
    const USAFlag = (<span role="img" aria-label="USA">🇺🇸USA</span>);
    let currentLocale = USAFlag;
    let usaClass = 'current';
    let chinaClass = '';
    if (L.currentLocale === 'zh-CN') {
      currentLocale = ChinaFlag;
      usaClass = '';
      chinaClass = 'current';
    }
    const localesList = (
      <ul className="dropdown-menu dropdown-menu-right" aria-labelledby="nav-dropdown">
        <li className="dropdown-header">{L.get("Country and Language")}</li>
          <li><a onClick={this.setCurrentLocale.bind(this)} className={usaClass} lang='en-US'>{USAFlag}</a></li>
        <li><a onClick={this.setCurrentLocale.bind(this)} className={chinaClass} lang='zh-CN'>{ChinaFlag}</a></li>
         {/* <li role="separater" className="divider"></li> 
         <li><a>China-Hongkong</a></li>  */}
      </ul>
    );
    const navigationItem = (
    <div className={menuClass}>
      <ul className="nav navbar-nav navbar-left">
        <li className={homeClass}><a href="/">{L.get("Home")}</a></li>
        <li className={productsClass}><a href="/products">{L.get("Products")}</a></li>
        <li className={contactClass}><a href="/contact">{L.get("CONTACT")}</a></li>
        <li className={aboutClass}><a href="/about">{L.get("About")}</a></li>
      </ul>
      <ul className="nav navbar-nav navbar-right">
        {/* <li><a href="/showcase/?theme=1">Showcase</a></li>
        <li><a href="/integrations/?theme=1">Integrations</a></li> */}
        <li className={localesClass}>
          <a className="dropdown-toggle" id="nav-dropdown" onClick={this.toggleOpenLocales.bind(this)} href="#1" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
          {currentLocale} <span className="caret"></span></a>
          {localesList}
        </li>
      </ul>
    </div>
    );

    return (
      <div className="navbar navbar-default navbar-static-top">
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-3 col-md-2 col-sm-3">
              {logoItem}
          </div>
          <div className="col-lg-9 col-md-10 col-sm-9">
            {navigationItem}
          </div>
        </div>
      </div>
    </div>
    );
  }
}

export default AppHeader;



