import React, { Component } from 'react';
import './AppFooter.css';

class AppFooter extends Component {
  render() {
    const L = window.Locales;
    return (
      <footer className="Footer"> 
        <div id="band1"></div>
        <div id="band2"></div>
        <div className="container"> 
          <ul className="Footer-links"> 
            <li><a href="https://github.com/oueway/oueway.github.io/issues/new" target="_blank" rel='noopener noreferrer'>{L.get("REPORT_ISSUE")}</a></li>
            <li><a href="/contact" >{L.get("CONTACT")}</a></li>
            <li><a href="/privacy" >{L.get("Privacy")}</a></li>
            <li><a href="/about" >{L.get("About")}</a></li>
          </ul> 
          <p>
            {L.get("COPYRIGHT", {"brand": L.get("BRAND")})}
          </p> 
        </div> 
      </footer>
    );
  }
}

export default AppFooter;



